@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #dfe3e6;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.user-card{
    text-align: center;
    cursor: pointer;
    height: 60px;
    background-color: white;
    margin: 10px 5px; 
    border-radius: 5px;
    box-shadow: 0 5px 8px rgba(9,45,66,.15);
    color: #343a40;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.user-card:hover{
    background-color: #F29A32;
    color: white;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.user-name{
    padding: 10px 20px;
    font-size: 12px;
    font-weight: normal;
}

.modal-image{
    width: 80px;
    height: 80px;
    cursor: pointer;
}
.modal-title{
    font-weight: bold;
    font-size: 15px;
}
.modal-subtitle{
    font-weight: normal;
    font-size: 12px;
}
.modal-repositories{
    color: #219267;
    font-size: 12px;
    font-weight: normal;
}
.modal-button{
    border-width: 0;
    color: white;
    font-weight: bold;
    background-color: #F29A32;
    border-radius: 5px;
    padding: 5px 10px;
    cursor: pointer;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.modal-button:hover{
    background-color: #42D79E;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
@media only screen and (max-width:500px){
    .user-name{
        padding: 10px 10px;
        font-size: 11px;
        font-weight: normal;
    }
}
.list{
    display: grid;
    grid-template-columns: auto auto auto auto auto auto;
}
@media only screen and (max-width:500px){
    .list{
        display: grid;
        grid-template-columns: auto auto;
    }
}
.dashboard{
    display: flex;
    background-image: -webkit-linear-gradient(45deg, #3edc93 0%, #53c2d4 58%, #57bce2 100%);
    background-image: linear-gradient(45deg, #3edc93 0%, #53c2d4 58%, #57bce2 100%);
    width: 100vw;
    height: calc(100vh - 70px);
}
.dashboard-container{
    display: inline-block;
    width: auto;
    height: auto;
    margin: auto auto;
    background-color: #343a40;
    box-shadow: 0px 5px 5px #01454f80;
    padding: 50px;
    border-radius: 5px;
}

.github-logo{
    display: flex;
}
.github-logo img{
    width: 100px;
    height: 100px;
    margin: auto auto 20px auto;
}

.form{
    display: inline-block;
    width: 200px;
}
.form-title{
    font-size: 12px;
    color: white; 
    font-weight: normal;
    margin-bottom: 1px;
}
.form-input{
    border-width: 0;
    height: 25px;
    padding-left: 10px;
    width: 100%;
    margin-bottom: 10px;
    border-radius: 5px;
}
input:focus{
    outline: none;
}
.form-button{
    display: flex; 
    background-color: #42D79E;
    border-width: 0;
    color: white;
    margin-top: 10px;
    height: 30px;
    width: 80px;
    border-radius: 5px;
    cursor: pointer;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.form-button:hover{
    display: flex; 
    background-color: #F29A32;
    border-width: 0;
    color: white;
    margin-top: 10px;
    height: 30px;
    width: 80px;
    border-radius: 5px;
    cursor: pointer;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}
.form-button h1{
    margin: auto auto;
    font-size: 13px;
    font-weight: bold;
}
.navbar{
    position: relative;
    display: flex;
    margin: auto auto; 
    width: 100vw;
    height: 70px;
    background-color: #01444F;
}

.logo{
    margin: auto auto;
}
